import { getImgFromHtml, html2text } from './util';
import { ListItem } from '@portal-site/types';
export const listMapper = (source: any[]): ListItem[] => {
    return source.map(
        (item): ListItem => {
            return {
                $raw_data: item,
                id: item.archivesId, // ÊñáÁ´†id
                channelNo: item.channelNo, // Ê†èÁõÆÁºñÂè∑
                channelName: item.channelName, // Ê†èÁõÆÂêçÁß∞
                title: item.title, // Ê†áÈ¢ò
                subTitle: item.subTitle, // ÂâØÊ†áÈ¢ò
                publishDate: item.publishDate.split(' ')[0],
                parentChannelNo: item.parentChannelNo, // Áà∂Ê†èÁõÆ
                imgSrc: encodeURI(item.thumbImg ? item.thumbImg : getImgFromHtml(item.content)),
                appImgSrc: encodeURI(item.thumbAppImg ?? ''), // appÁº©Áï•ÂõæÔºåÈªòËÆ§‰∏∫'' Â≠óÁ¨¶‰∏≤‰Ωú‰∏∫ URI ËøõË°åÁºñÁ†Å
                content: item.content, // ËØ¶ÊÉÖ
                $content: html2text(item.content),
                abstract: item.description, // ÊëòË¶ÅÂÜÖÂÆπ
                source: item.source ?? '', // ÊñáÁ´†Êù•Ê∫ê
                linkUrl: item.linkUrl ?? '', // Ëá™ÂÆö‰πâÈìæÊé•,
                isShowTitle: item.isShowTitle, // ÊòØÂê¶ÊòæÁ§∫Ê†áÈ¢ò
                visitCount: item.visitCount
            };
        }
    );
};
